"use client";

import { useState, useEffect } from "react";
import { useAuth } from "@/hooks/useAuth";
import API from "@/lib/api";
import { useRouter } from "next/navigation";
import Link from "next/link";
import {
  FiShoppingBag,
  FiPackage,
  FiUsers,
  FiDollarSign,
  FiBox,
  FiFileText,
  FiTrendingUp,
  FiClock,
  FiArrowRight,
  FiStar,
  FiCalendar,
} from "react-icons/fi";

export default function AdminDashboard() {
  const { user } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  const isAdmin = user?.role === "admin";
  const isStaff = user?.role === "staff";

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    try {
      const res = await API.get("/stats/dashboard");
      setStats(res.data.stats || res.data);
    } catch (error: any) {
      console.error("Failed to load dashboard stats:", error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center py-20">
        <div className="h-10 w-10 animate-spin rounded-full border-4 border-[var(--primary)] border-t-transparent" />
      </div>
    );
  }

  if (!stats) {
    return (
      <div className="flex flex-col items-center justify-center py-20 text-gray-400">
        <FiTrendingUp className="mb-3 h-12 w-12" />
        <p className="text-lg font-medium">Unable to load dashboard data</p>
        <button
          onClick={fetchStats}
          className="mt-4 rounded-lg bg-[var(--primary)] px-4 py-2 text-sm font-medium text-white hover:opacity-90"
        >
          Retry
        </button>
      </div>
    );
  }

  const statCards = [
    {
      label: "Total Orders",
      value: stats.totalOrders ?? 0,
      icon: FiShoppingBag,
      iconBg: "bg-[#c47a5a]/10",
      iconColor: "text-[#c47a5a]",
      show: true,
    },
    {
      label: "Today's Orders",
      value: stats.todayOrders ?? 0,
      icon: FiCalendar,
      iconBg: "bg-indigo-100",
      iconColor: "text-indigo-600",
      show: true,
    },
    {
      label: "Pending Orders",
      value: stats.pendingOrders ?? 0,
      icon: FiClock,
      iconBg: "bg-yellow-100",
      iconColor: "text-yellow-600",
      show: true,
    },
    {
      label: "Total Products",
      value: stats.totalProducts ?? 0,
      icon: FiBox,
      iconBg: "bg-blue-100",
      iconColor: "text-blue-600",
      show: true,
    },
    {
      label: "Total Users",
      value: stats.totalUsers ?? 0,
      icon: FiUsers,
      iconBg: "bg-purple-100",
      iconColor: "text-purple-600",
      show: isAdmin,
    },
    {
      label: "Total Revenue",
      value: `₹${(stats.totalRevenue ?? 0).toLocaleString("en-IN", { minimumFractionDigits: 2 })}`,
      icon: FiDollarSign,
      iconBg: "bg-green-100",
      iconColor: "text-green-600",
      show: isAdmin,
    },
    {
      label: "Total Bills",
      value: stats.totalBills ?? 0,
      icon: FiFileText,
      iconBg: "bg-rose-100",
      iconColor: "text-rose-600",
      show: isAdmin,
    },
  ];

  const quickLinks = [
    { label: "Manage Orders", href: "/admin/orders", icon: FiShoppingBag, show: true },
    { label: "Manage Products", href: "/admin/products", icon: FiPackage, show: true },
    { label: "Manage Categories", href: "/admin/categories", icon: FiBox, show: true },
    { label: "Manage Users", href: "/admin/users", icon: FiUsers, show: isAdmin },
    { label: "Manage Bills", href: "/admin/bills", icon: FiFileText, show: isAdmin },
    { label: "Manage Reviews", href: "/admin/reviews", icon: FiStar, show: isAdmin },
  ];

  const dailyOrders = stats.dailyOrders || [];
  const ordersByStatus = stats.ordersByStatus || {};
  const topProducts = stats.topProducts || [];

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-gray-800">
          {isStaff ? "Staff" : "Admin"} Dashboard
        </h1>
        <p className="mt-1 text-gray-500">
          Welcome back, {user?.name?.split(" ")[0] || "Admin"}. Here&apos;s your overview.
        </p>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 gap-4 md:grid-cols-3 lg:grid-cols-4">
        {statCards
          .filter((card) => card.show)
          .map((card) => (
            <div
              key={card.label}
              className="flex items-center gap-4 rounded-xl bg-white p-5 shadow-sm"
            >
              <div
                className={`flex h-12 w-12 shrink-0 items-center justify-center rounded-lg ${card.iconBg}`}
              >
                <card.icon className={`h-6 w-6 ${card.iconColor}`} />
              </div>
              <div className="min-w-0">
                <p className="truncate text-sm text-gray-500">{card.label}</p>
                <p className="text-2xl font-bold text-gray-800">{card.value}</p>
              </div>
            </div>
          ))}
      </div>

      {/* Charts Section */}
      <div className="grid grid-cols-1 gap-6 lg:grid-cols-2">
        {/* Daily Orders Bar Chart */}
        {dailyOrders.length > 0 && (
          <div className="bg-white rounded-xl shadow-sm p-6">
            <h3 className="text-sm font-semibold text-gray-500 uppercase mb-4">
              Orders This Week
            </h3>
            <div className="h-48 flex items-end gap-2">
              {dailyOrders.map((day: any) => {
                const maxCount = Math.max(
                  ...dailyOrders.map((d: any) => d.count),
                  1
                );
                const height = (day.count / maxCount) * 100;
                return (
                  <div
                    key={day._id}
                    className="flex-1 flex flex-col items-center gap-1"
                  >
                    <span className="text-xs font-medium text-gray-600">
                      {day.count}
                    </span>
                    <div
                      className="w-full bg-[var(--primary)] rounded-t-md transition-all min-h-[4px]"
                      style={{ height: `${height}%` }}
                    />
                    <span className="text-xs text-gray-400">
                      {day._id.slice(5)}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>
        )}

        {/* Order Status Distribution */}
        {Object.keys(ordersByStatus).length > 0 && (
          <div className="bg-white rounded-xl shadow-sm p-6">
            <h3 className="text-sm font-semibold text-gray-500 uppercase mb-4">
              Order Status
            </h3>
            <div className="space-y-3">
              {Object.entries(ordersByStatus).map(
                ([status, count]: [string, any]) => {
                  const total = stats.totalOrders || 1;
                  const percentage = (count / total) * 100;
                  const colors: Record<string, string> = {
                    pending: "#f59e0b",
                    confirmed: "#3b82f6",
                    preparing: "#f97316",
                    served: "#8b5cf6",
                    completed: "#22c55e",
                    cancelled: "#ef4444",
                  };
                  return (
                    <div key={status}>
                      <div className="flex justify-between text-sm mb-1">
                        <span className="capitalize text-gray-600">
                          {status}
                        </span>
                        <span className="font-medium">{count}</span>
                      </div>
                      <div className="h-2.5 bg-gray-100 rounded-full overflow-hidden">
                        <div
                          className="h-full rounded-full transition-all"
                          style={{
                            width: `${percentage}%`,
                            backgroundColor:
                              colors[status] || "#c47a5a",
                          }}
                        />
                      </div>
                    </div>
                  );
                }
              )}
            </div>
          </div>
        )}

        {/* Top Products - Admin only */}
        {isAdmin && topProducts.length > 0 && (
          <div className="bg-white rounded-xl shadow-sm p-6">
            <h3 className="text-sm font-semibold text-gray-500 uppercase mb-4">
              Top Products
            </h3>
            <div className="space-y-3">
              {topProducts.map((p: any, i: number) => {
                const max = topProducts[0]?.totalQuantity || 1;
                return (
                  <div key={i}>
                    <div className="flex justify-between text-sm mb-1">
                      <span className="text-gray-600 truncate mr-2">
                        {p._id}
                      </span>
                      <span className="font-medium text-[var(--primary)] whitespace-nowrap">
                        {p.totalQuantity} sold
                      </span>
                    </div>
                    <div className="h-2 bg-gray-100 rounded-full overflow-hidden">
                      <div
                        className="h-full bg-[var(--primary)] rounded-full"
                        style={{
                          width: `${(p.totalQuantity / max) * 100}%`,
                        }}
                      />
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        )}

        {/* Revenue This Week - Admin only */}
        {isAdmin && dailyOrders.length > 0 && (
          <div className="bg-white rounded-xl shadow-sm p-6">
            <h3 className="text-sm font-semibold text-gray-500 uppercase mb-4">
              Revenue This Week
            </h3>
            <div className="h-48 flex items-end gap-2">
              {dailyOrders.map((day: any) => {
                const maxRevenue = Math.max(
                  ...dailyOrders.map((d: any) => d.revenue || 0),
                  1
                );
                const revenue = day.revenue || 0;
                const height = (revenue / maxRevenue) * 100;
                return (
                  <div
                    key={day._id}
                    className="flex-1 flex flex-col items-center gap-1"
                  >
                    <span className="text-xs font-medium text-gray-600">
                      ₹{revenue.toLocaleString("en-IN")}
                    </span>
                    <div
                      className="w-full bg-emerald-500 rounded-t-md transition-all min-h-[4px]"
                      style={{ height: `${height}%` }}
                    />
                    <span className="text-xs text-gray-400">
                      {day._id.slice(5)}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </div>

      {/* Today's Orders - Prominent for Staff */}
      {isStaff && (
        <div className="rounded-xl bg-white p-6 shadow-sm">
          <div className="flex items-center justify-between mb-2">
            <h3 className="text-lg font-semibold text-gray-800">
              Today&apos;s Orders
            </h3>
            <Link
              href="/admin/orders"
              className="flex items-center gap-1 text-sm font-medium text-[var(--primary)] hover:underline"
            >
              View All <FiArrowRight className="h-4 w-4" />
            </Link>
          </div>
          <div className="flex items-center gap-4">
            <div className="flex h-16 w-16 items-center justify-center rounded-xl bg-indigo-100">
              <FiCalendar className="h-8 w-8 text-indigo-600" />
            </div>
            <div>
              <p className="text-3xl font-bold text-gray-800">
                {stats.todayOrders ?? 0}
              </p>
              <p className="text-sm text-gray-500">orders placed today</p>
            </div>
          </div>
        </div>
      )}

      {/* Quick Links */}
      <div className="rounded-xl bg-white p-6 shadow-sm">
        <h2 className="mb-4 text-lg font-semibold text-gray-800">
          Quick Links
        </h2>
        <div className="grid grid-cols-1 gap-2 sm:grid-cols-2 lg:grid-cols-3">
          {quickLinks
            .filter((link) => link.show)
            .map((link) => (
              <Link
                key={link.label}
                href={link.href}
                className="flex items-center justify-between rounded-lg border border-gray-100 p-3 transition-colors hover:bg-[var(--primary)]/5 hover:border-[var(--primary)]/20"
              >
                <div className="flex items-center gap-3">
                  <link.icon className="h-5 w-5 text-[var(--primary)]" />
                  <span className="text-sm font-medium text-gray-700">
                    {link.label}
                  </span>
                </div>
                <FiArrowRight className="h-4 w-4 text-gray-400" />
              </Link>
            ))}
        </div>
      </div>
    </div>
  );
}
